import { getSearchTranslation } from "../helpers/globals";
export default class ResetFilters {
    constructor(activeFilters) {
        this.element = createButton();
        this.hide();
        this.activeFilters = activeFilters;
        this.initEvents();
    }
    initEvents() {
        this.element.addEventListener('click', (e) => {
            e.preventDefault();
            let UrlParts = AC.list_screen_link.split('?');
            let search = new URLSearchParams(UrlParts.length === 2 ? UrlParts[1] : '');
            search.append('ac-actions-form', "1");
            window.location.href = UrlParts[0] + '?' + search.toString();
        });
        this.activeFilters.onRefresh(() => {
            if (this.activeFilters.hasActiveFilters()) {
                this.show();
            }
            else {
                this.hide();
            }
        });
    }
    getElement() {
        return this.element;
    }
    show() {
        this.element.style.display = 'inline-block';
    }
    hide() {
        this.element.style.display = 'none';
    }
}
const createButton = () => {
    let element = document.createElement('a');
    element.classList.add('ac-clear-filters');
    element.href = '#';
    element.innerHTML = getSearchTranslation().clear_filters;
    return element;
};
