import { writable } from 'svelte/store';
function createRules() {
    const { subscribe, set, update } = writable([]);
    return {
        subscribe,
        set,
        update,
        delete: (id) => update(items => {
            items = items.filter(d => d.uid !== id);
            return items;
        }),
        addRule: (rule) => update(items => {
            return [...items, rule];
        })
    };
}
function createOpenRulesStore() {
    const { subscribe, set, update } = writable([]);
    return {
        subscribe,
        set,
        update,
        open: (id) => update(items => {
            return [...items, id];
        }),
        close: (id) => update(items => {
            return items.filter(d => d !== id);
        }),
    };
}
function createFormattedValueStore() {
    const { subscribe, set, update } = writable({});
    return {
        subscribe,
        set,
        update,
        setFormattedValue: (id, value) => update(items => {
            Object.assign(items, { [id]: value });
            return items;
        }),
    };
}
export const rulesStore = createRules();
export const openRulesStore = createOpenRulesStore();
export const formattedValueStore = createFormattedValueStore();
