<script lang="ts">

    import {createEventDispatcher, onMount} from "svelte";
    import {createSegment, createSegmentResponse} from "../modules/segments/ajax";
    import QueryParams from "../modules/segments/query-params";
    import {canManageSegments, segments} from "../store/segments";
    import AcServices from "AC/modules/ac-services";
    import {getSearchTranslation} from "../helpers/globals";
    import {LocalizedAcTable} from "AC/types/table";

    const dispatch = createEventDispatcher();
    const i18n = getSearchTranslation();

    declare const AC_SERVICES: AcServices;
    declare const AC: LocalizedAcTable;

    let loading = false;
    let segmentName = '';
    let segmentGlobal = false;
    let modal;
    let nameInput;
    let error = '';

    const close = () => {
        dispatch('close', {});
    }

    const save = () => {
        let queryStringParams = new QueryParams();
        loading = true;

        createSegment(
            segmentName,
            segmentGlobal,
            queryStringParams.get().toString(),
            AC.ajax_nonce, AC.layout
        ).then(response => {
            let data: createSegmentResponse = response.data;

            if (data.success === true) {
                segments.addSegment(data.data.segment);
            } else {
                error = data.data.error;
            }
            close();
        }).catch(errorResponse => {
            error = errorResponse.message;
        }).then(() => {
            loading = false;
        });
    }

    onMount(() => {
        document.body.append(modal);
        nameInput.focus();

        document.addEventListener('keyup', (e) => {
            if ('Escape' === e.key) {
                close();
            }
        });
    })
</script>
<div class="ac-modal -active" id="ac-modal-create-segment" bind:this={modal} on:click={close} on:keydown role="none">
	<div class="ac-modal__dialog -create-segment" on:click|stopPropagation on:keydown role="none">
		<form id="frm_create_segment" on:submit|preventDefault={save}>
			<div class="ac-modal__dialog__header">
				{i18n.segments.save_filters}
				<button type="button" class="ac-modal__dialog__close" on:click|preventDefault={close}>
					<span class="dashicons dashicons-no-alt"></span>
				</button>
			</div>
			<div class="ac-modal__dialog__content" style="overflow: hidden;">
				<label for="inp_segment_name" class="screen-reader-text">Name</label>
				<input bind:this={nameInput} bind:value={segmentName} type="text" name="name" id="inp_segment_name" required autocomplete="off" placeholder="Name">

				{#if $canManageSegments }
					<br>
					<label>
						<input bind:checked={segmentGlobal} type="checkbox" name="global">Make available to all users
					</label>
				{/if}
				{#if error }
					<div class="ac-modal__error">
						{error}
					</div>
				{/if}
			</div>
			<div class="ac-modal__dialog__footer">
				{#if loading}
					<div class="ac-modal__loading" style="display: inline-block;">
						<span class="dashicons dashicons-update"></span>
					</div>
				{/if}
				<button class="button" type="button" on:click={close}>{i18n.segments.cancel}</button>
				<button type="submit" class="button button-primary">{i18n.segments.save}</button>
			</div>
		</form>
	</div>
</div>