<script lang="ts">
    import {valueValidator} from "../../types/global";
    import {createEventDispatcher, onMount} from "svelte";

    export let value: string = '';

    const dispatch = createEventDispatcher();
    export const validate: valueValidator = (): boolean => {
        return input.reportValidity();
    };

    let input: HTMLInputElement;
    let inputValue = ''

    const handleChange = () => {
        value = parseToAcFormat(inputValue);

        dispatch('formattedValue', new Date(inputValue).toLocaleString('default', { month: 'long', year: 'numeric' }) );
    }

    const parseToAcFormat = (v: string): string => {
        return v.substring(0, 4) + v.substring(5, 7);
    }

    const parseFromAcFormat = (v): string => {
        return v.substring(0, 4) + '-' + v.substring(4, 6);
    }

    onMount(() => {
        if (value.length === 6) {
            inputValue = parseFromAcFormat(value);
        }
    })

</script>

<input type="month" bind:value={inputValue} bind:this={input} on:change={handleChange}/>