<script lang="ts">
    import Select from "svelte-select";
    import {createEventDispatcher, onMount, tick} from "svelte";
    import {SearchFilterOptions} from "../../ajax/select-values";
    import {formattedValueStore} from "../../store/rules";
    import {FilterType} from "../../types/filters";
    import {valueValidator} from "../../types/global";
    import FiltersMapper from "../../mapping/filters-mapper";
    import {getSearchTranslation} from "../../helpers/globals";
    import NoResults from "../../../../core/js/component/select/NoResults.svelte";

    export let value: string | any = '';
    export let filter: FilterType;
    export let uid: string;

    export const validate: valueValidator = (): boolean => {
        return typeof value === 'string' && value !== '';
    };

    export const focus = async () => {
        await tick();
        listOpen = true;
        focused = true;
    }

    const i18n = getSearchTranslation();
    const dispatch = createEventDispatcher();
    const groupBy = (item) => item.group;

    let focused = false;
    let loading: boolean = false;
    let emptyLabel = i18n.no_search_results;
    let selectValue = null;
    let hideEmptyState: boolean = false;
    let listOpen: boolean = false;
    let search = new SearchFilterOptions(filter.id)

    async function searchItems(d) {
        let data = await search.search(d);
        let items = FiltersMapper.fromSelect2(data.items);

        if (data.more) {
            items.push({value: null, label: i18n.more_search_records, selectable: false})
        }

        return items;
    }

    let changeHandler = (e) => {
        value = e.detail.value;
        dispatch('formattedValue', e.detail.label);
    }

    onMount(async () => {
        if (value) {
            selectValue = {value: value, label: $formattedValueStore[uid] ?? value}
        }
        await tick();
        listOpen = true;
    });
</script>

<Select
		{groupBy}
		{loading}
		{hideEmptyState}
		{focused}
		loadOptions={searchItems}
		showChevron
		listAutoWidth={false}
		clearable={false}
		class="-wordpress"
		on:change={changeHandler}
		bind:value={selectValue}
		bind:listOpen={listOpen}
		--list-z-index="1000"
		--width="200px"
>
	<div slot="empty">
		<NoResults label={emptyLabel}></NoResults>
	</div>
</Select>
