<script lang="ts">
    import {valueValidator} from "../../types/global";
    import {onMount} from "svelte";

    export let value: Array<number | null> = [];

    export const validate: valueValidator = (): boolean => {
        let validStart = inputStart.reportValidity();
        let validEnd = inputEnd.reportValidity();

        return validStart && validEnd;
    };

    export const focus = (): void => {
        inputStart.focus();
    }

    let inputStart: HTMLInputElement;
    let inputEnd: HTMLInputElement;
    let valueStart = Array.isArray(value) ? value[0] : null ?? null;
    let valueEnd = Array.isArray(value) ? value[1] : null ?? null;

    const updateValue = (first: boolean) => {
        let startDate = new Date(inputStart.value);
        let endDate = new Date(inputEnd.value);

        if (first) {
            if (endDate && endDate < startDate) {
                valueEnd = valueStart;
            }
        } else {
            if (startDate && startDate > endDate) {
                valueStart = valueEnd;
            }
        }

        value = [valueStart, valueEnd]
    }

    onMount(() => {
        if (!Array.isArray(value)) {
            value = [];
        }
    })
</script>

<div class="acp-smartfilters-value-group">
	<span class="acp-smartfilters-value-group__value -start">
		<input type="date" bind:value={valueStart} on:input={() => updateValue(true) } bind:this={inputStart} required>
	</span>
	<span class="acp-smartfilters-value-group__sep">-
	</span>
	<span class="acp-smartfilters-value-group__value -end">
		<input type="date" bind:value={valueEnd} on:input={() => updateValue(false) } bind:this={inputEnd} required>
	</span>
</div>
