<script lang="ts">
    import {segments} from "../store/segments";
    import SegmentItem from "./SegmentItem.svelte";
    import AddSegmentModal from "./AddSegmentModal.svelte";
    import {createEventDispatcher, onMount, tick} from "svelte";
    import {getAcServices, getSearchTranslation} from "../helpers/globals";
    import {getOffset} from "../helpers/document";

    const i18n = getSearchTranslation();
    const dispatch = createEventDispatcher();

    export let trigger: HTMLElement | null = null;

    let addButton: HTMLElement | null;
    let component: HTMLElement;
    let opened = true;
    let pointer;
    let items = [];
    let personalItems;
    let publicItems;
    let showModal = false;
    let position = {top: '50%', left: '50%'};

    const moveToElement = (element: HTMLElement) => {
        let offset = getOffset(element);

        position = {
            top: (offset.top + 33).toString() + 'px',
            left: (offset.left + Math.round(element.offsetWidth / 2) + 19).toString() + 'px'
        }
    }

    export const open = () => {
        opened = true;
        if (trigger) {
            moveToElement(trigger);
        }
        setTimeout(() => initPointer(), 100);
    }

    export const close = () => {
        opened = false;
    }


    segments.subscribe(value => {
        items = value;
    });

    const openModal = () => {
        showModal = true;
    }

    const dispatchClose = () => {
        document.removeEventListener('click', dispatchClose);
        dispatch('close');
    }

    const initPointer = () => {
        const services = getAcServices();

        if (services.hasService('initPointers')) {
            services.getService('initPointers')(document.querySelectorAll('.ac-segments__instructions .ac-pointer'));
        }
    }

    onMount(async () => {
        initPointer();

        if (trigger) {
            moveToElement(trigger);
        }

        document.body.append(component);
        document.addEventListener('click', dispatchClose);
        initPointer();

        await tick();
        addButton?.focus();

    })

    $: personalItems = items.filter((d) => !d.global);
    $: publicItems = items.filter((d) => d.global);
</script>

<div class="ac-segments -admin -active" style="position: absolute; left: {position.left}; top: {position.top};" on:click|stopPropagation bind:this={component} on:keydown role="none">
	<div class="ac-segments__create">
		<span class="cpac_icons-segment"></span>
		<button class="button button-primary" on:click|preventDefault={openModal} type="button" bind:this={addButton}>{i18n.segments.save_filters}</button>
	</div>

	{#if personalItems.length > 0 }
		<div class="ac-segments__list -personal">
			<div class="ac-segments__list__items">

				{#each personalItems as item}
					<SegmentItem key={item.key} name={item.name} url={item.url}></SegmentItem>
				{/each}

			</div>
		</div>
	{/if}

	{#if publicItems.length > 0 }
		<div class="ac-segments__list -global -visible">
			<div class="ac-segments__list__label">{i18n.segments.public_filters}</div>
			<div class="ac-segments__list__items">
				{#each publicItems as item}
					<SegmentItem key={item.key} name={item.name} url={item.url}></SegmentItem>
				{/each}
			</div>
		</div>
	{/if}

	<div class="ac-segments__instructions">
		<span class="ac-pointer instructions" rel="pointer-filtered_segments" data-pos="right" data-width="300">{i18n.segments.instructions}</span>
	</div>

</div>
{#if showModal }
	<AddSegmentModal on:close={ () => showModal = false }></AddSegmentModal>
{/if}
