import axios from "axios";
export const fetchRemoteOptionsForColumn = (column) => {
    return new Promise((resolve, reject) => {
        axios.get(ajaxurl, {
            params: {
                action: 'acp_search_comparison_request',
                method: 'get_options',
                layout: AC.layout,
                column: column,
                list_screen: AC.list_screen,
                _ajax_nonce: AC.ajax_nonce
            }
        }).then((result) => {
            switch (result.data.success) {
                case true:
                    resolve(result.data.data.results);
                    break;
                case false:
                    reject(result.data.data);
            }
        }).catch(d => {
            reject(d.message);
        });
    });
};
export class SearchFilterOptions {
    constructor(column) {
        this.column = column;
    }
    search(query) {
        return new Promise((resolve, reject) => {
            axios.get(ajaxurl, {
                params: {
                    action: 'acp_search_comparison_request',
                    method: 'get_options',
                    layout: AC.layout,
                    searchterm: query,
                    column: this.column,
                    list_screen: AC.list_screen,
                    _ajax_nonce: AC.ajax_nonce
                }
            }).then((result) => {
                switch (result.data.success) {
                    case true:
                        resolve({
                            items: result.data.data.results,
                            more: result.data.data.pagination.more
                        });
                        break;
                    case false:
                        reject(result.data.data);
                }
            }).catch(d => {
                reject(d.message);
            });
        });
    }
}
