import SelectValue from "../component/filter-value/SelectValue.svelte";
import TextValue from "../component/filter-value/TextValue.svelte";
import NumericRange from "../component/filter-value/NumericRange.svelte";
import DaysAgo from "../component/filter-value/DaysAgo.svelte";
import DateMonth from "../component/filter-value/MonthValue.svelte";
import YearValue from "../component/filter-value/YearValue.svelte";
import DateWithin from "../component/filter-value/DaysWithin.svelte";
import DateValue from "../component/filter-value/DateValue.svelte";
import DateRange from "../component/filter-value/DateRange.svelte";
import { FILTER_TYPES, OPERATOR_TYPES } from "../const";
import NumericValue from "../component/filter-value/NumericValue.svelte";
import NullValue from "../component/filter-value/NullValue.svelte";
import RemoteSelectValue from "../component/filter-value/RemoteSelectValue.svelte";
import SearchableSelectValue from "../component/filter-value/SearchableSelectValue.svelte";
export const valueComponentByOperator = (operator, filter) => {
    switch (filter.type) {
        case FILTER_TYPES.STRING:
            return getStringValueComponent(operator, filter);
        case FILTER_TYPES.DATE:
            return getDateValueComponent(operator, filter);
        case FILTER_TYPES.INT:
        case FILTER_TYPES.DOUBLE:
            return getNumericValueComponent(operator, filter);
    }
    return null;
};
const getStringValueComponent = (operator, filter) => {
    switch (operator) {
        case OPERATOR_TYPES.EQ:
        case OPERATOR_TYPES.NEQ:
            let component = getSelectValueComponent(filter);
            return component !== null
                ? component
                : TextValue;
        case OPERATOR_TYPES.CONTAINS:
        case OPERATOR_TYPES.NOT_CONTAINS:
        case OPERATOR_TYPES.BEGINS_WITH:
        case OPERATOR_TYPES.ENDS_WITH:
            return TextValue;
        default:
            return NullValue;
    }
};
const getSelectValueComponent = (filter) => {
    if (filter.use_ajax) {
        return filter.use_pagination
            ? SearchableSelectValue
            : RemoteSelectValue;
    }
    return filter.values !== false
        ? SelectValue
        : null;
};
const getNumericValueComponent = (operator, filter) => {
    switch (operator) {
        case OPERATOR_TYPES.EQ:
        case OPERATOR_TYPES.NEQ:
            let component = getSelectValueComponent(filter);
            return component !== null
                ? component
                : NumericValue;
        case OPERATOR_TYPES.LT:
        case OPERATOR_TYPES.LTE:
        case OPERATOR_TYPES.GT:
        case OPERATOR_TYPES.GTE:
            return NumericValue;
        case OPERATOR_TYPES.BETWEEN:
            return NumericRange;
        default:
            return NullValue;
    }
};
const getDateValueComponent = (operator, filter) => {
    switch (operator) {
        case OPERATOR_TYPES.EQ_YEAR:
            return YearValue;
        case OPERATOR_TYPES.EQ_MONTH:
            return DateMonth;
        case OPERATOR_TYPES.WITHIN_DAYS:
            return DateWithin;
        case OPERATOR_TYPES.LT_DAYS_AGO:
        case OPERATOR_TYPES.GT_DAYS_AGO:
            return DaysAgo;
        case OPERATOR_TYPES.BETWEEN:
            return DateRange;
        case OPERATOR_TYPES.EQ:
        case OPERATOR_TYPES.NEQ:
        case OPERATOR_TYPES.GT:
        case OPERATOR_TYPES.GTE:
        case OPERATOR_TYPES.LT:
        case OPERATOR_TYPES.LTE:
            return DateValue;
        case OPERATOR_TYPES.TODAY:
        case OPERATOR_TYPES.FUTURE:
        case OPERATOR_TYPES.PAST:
        case OPERATOR_TYPES.CONTAINS:
        default:
            return NullValue;
    }
};
