<script lang="ts">
    import {valueValidator} from "../../types/global";
    import {onMount} from "svelte";

    export let value: Array<number | null> = [];

    export const validate: valueValidator = (): boolean => {
        let validStart = inputStart.reportValidity();
        let validEnd = inputEnd.reportValidity();

        return validStart && validEnd;
    };

    export const focus = (): void => {
        inputStart.focus();
    }

    const ensureFloatOrNull = (value: number | null | string): number | null => {
        return value === null || typeof value === 'undefined' ? null : parseFloat(value.toString());
    }

    let inputStart: HTMLInputElement;
    let inputEnd: HTMLInputElement;
    let valueStart = Array.isArray(value) ? ensureFloatOrNull(value[0]) : null ?? null;
    let valueEnd = Array.isArray(value) ? ensureFloatOrNull(value[1]) : null ?? null;

    const updateValue = (first: boolean) => {
        if (first) {
            if (valueEnd && valueEnd < valueStart) {
                valueEnd = valueStart;
            }
        } else {
            if (valueStart && valueStart > valueEnd) {
                valueStart = valueEnd;
            }
        }

        value = [
            ensureFloatOrNull(valueStart),
            ensureFloatOrNull(valueEnd)
        ]
    }

    onMount(() => {
        if (!Array.isArray(value)) {
            value = [];
        }
    })
</script>

<div class="acp-smartfilters-value-group">
	<span class="acp-smartfilters-value-group__value -start">
		<input type="number" bind:value={valueStart} on:blur={() => updateValue(true) } bind:this={inputStart} required step="any">
	</span>
	<span class="acp-smartfilters-value-group__sep">-
	</span>
	<span class="acp-smartfilters-value-group__value -end">
		<input type="number" bind:value={valueEnd} on:blur={() => updateValue(false) } bind:this={inputEnd} required step="any">
	</span>
</div>
