import AcHtmlElement from "AC/helpers/html-element";
import { getGlobalTranslation } from "AC/global-translations";
export const getFilterButton = () => {
    let button = document.querySelector('input[type=submit][name=acp_filter_action]');
    if (button) {
        return new AcHtmlElement(button);
    }
    return createFilterButton();
};
export const createFilterButton = () => {
    return AcHtmlElement.create('input').setAttributes({
        type: 'submit',
        name: 'acp_filter_action',
        value: getGlobalTranslation().table.filter
    }).addClasses('button', 'ac-search-button');
};
