import { getSearchConfig } from "../../helpers/globals";
import { initAcServices } from "AC/helpers/admin-columns";
export default class QueryParams {
    constructor() {
        this.service = initAcServices();
        this.query = new URLSearchParams(window.location.search);
        this.handleSorting();
        this.handleSearch();
    }
    handleSorting() {
        const sorting = getSearchConfig().sorting;
        if (!this.query.get('orderby') && sorting.orderby !== null) {
            this.query.set('orderby', sorting.orderby);
        }
        if (!this.query.get('order') && sorting.order !== null) {
            this.query.set('order', sorting.order);
        }
    }
    handleSearch() {
        if (!this.service.hasService('Search')) {
            return;
        }
        const search = this.service.getService('Search');
        this.query.set('ac-rules', search.getSerializedRules());
    }
    get() {
        return this.query;
    }
}
