import WhitelistedUrlParams from "./whitelisted-url-params";
import axios from "axios";
const action = 'acp_search_segment_request';
const deleteSegment = (key) => {
    let data = {
        _ajax_nonce: AC.ajax_nonce,
        action: action,
        list_screen: AC.list_screen,
        layout: AC.layout,
        key: key,
        method: 'delete',
    };
    return axios.post(ajaxurl, mapDataToFormData(data));
};
const retrieveSegments = () => {
    let data = {
        _ajax_nonce: AC.ajax_nonce,
        action: action,
        list_screen: AC.list_screen,
        layout: AC.layout,
        method: 'read',
    };
    return axios.get(ajaxurl, {
        params: data
    });
};
const createSegment = (name, global, querystring, nonce, layout_id) => {
    let data = {
        _ajax_nonce: nonce,
        action: action,
        layout: layout_id,
        method: 'create',
        name: name,
        global: global ? 1 : 0
    };
    let whiteListed = new WhitelistedUrlParams().getWhitelistedParams();
    let formData = mapDataToFormData(data);
    let whitelisted_qs = new URLSearchParams();
    whiteListed.forEach(k => {
        whitelisted_qs.append(k, '');
    });
    formData.append('query_string', querystring);
    formData.append('whitelisted_query_string', whitelisted_qs.toString());
    return axios.post(ajaxurl, formData);
};
const mapDataToFormData = (data, formData = null) => {
    if (formData === null) {
        formData = new FormData();
    }
    Object.keys(data).forEach(key => {
        formData.append(key, data[key]);
    });
    return formData;
};
export { deleteSegment, retrieveSegments, createSegment };
