<script lang="ts">
    import {createEventDispatcher, onMount, tick} from "svelte";
    import {valueComponentByOperator} from "../helpers/filter-value";
    import {formattedValueStore, openRulesStore} from "../store/rules";
    import {enterKey} from "../directive/enter-key";
    import {OPERATOR_TYPES} from "../const";
    import {getSearchTranslation} from "../helpers/globals";
    import {FilterType} from "../types/filters";

    const dispatch = createEventDispatcher();
    const i18n = getSearchTranslation();

    export let rule: any
    export let filter: FilterType;

    let isOpen: boolean = false;
    let valid: boolean = true;
    let valueComponent = valueComponentByOperator(rule?.operator, filter);
    let compactComponent: HTMLElement | null;
    let valueValidate: () => boolean;
    let valueFocus: () => void;

    const validate = () => {
        valid = valueValidate();
    }

    const open = async () => {
        isOpen = true;
        openRulesStore.open(rule.uid);
        dispatch('open');
        await tick();
        if (typeof valueFocus === 'function') valueFocus();

    }

    const close = async () => {
        isOpen = false;
        openRulesStore.close(rule.uid);
        dispatch('close');
        await tick();
        compactComponent?.focus();

    }

    const apply = () => {
        validate();
        if (valid === true) {
            close();
        }
    }

    const getOperatorLabel = (operator) => {
        return filter.operator_labels[operator] ?? operator;
    }

    const getOperatorSuffix = (operator) => {
        switch (operator) {
            case OPERATOR_TYPES.WITHIN_DAYS:
                return i18n.days;
            case OPERATOR_TYPES.GT_DAYS_AGO:
            case OPERATOR_TYPES.LT_DAYS_AGO:
                return i18n.days_ago;
            default:
                return null;
        }
    }

    const handleCompactKeyPress = (e: KeyboardEvent) => {
        if (e.key === 'Enter') {
            e.preventDefault();
            open();
        }
    }

    const handleCompactKeyUp = (e: KeyboardEvent) => {
        if (e.key === 'Delete') {
            e.preventDefault();
            deleteRule();
        }
    }

    const deleteRule = () => {
        dispatch('delete');
    }

    const updateValueComponent = () => {
        valueComponent = valueComponentByOperator(rule.operator, filter);
    }

    onMount(() => {
        if (isOpen) {
            open();
        }
    });

    $:isOpen = $openRulesStore.indexOf(rule && rule.uid) !== -1;
    $:operatorSuffix = getOperatorSuffix(rule && rule.operator);
</script>

{#if isOpen }
	<div class="acp-smartfilters-filter -form" class:-error={!valid} use:enterKey={{onlySelf:true}} on:enterpress={apply}>
		<span class="acp-smartfilters-filter__label">
			{filter.label}
		</span>

		<span class="acp-smartfilters-filter__operator">
		{#if filter.operators.length === 1}
			{getOperatorLabel( filter.operators[ 0 ] )}
		{:else }
			<select bind:value={rule.operator} on:change={updateValueComponent} on:keypress|stopPropagation|preventDefault={()=>{}}>
				{#each filter.operators as operator}
					<option value={operator}>{getOperatorLabel( operator )}</option>
				{/each}
			</select>
		{/if}
		</span>

		<span class="acp-smartfilters-filter__value" use:enterKey={{preventDefault:true}} on:enterpress={apply}>
			<svelte:component
					this={valueComponent}
					uid={rule.uid}
					bind:value={rule.value}
					bind:filter={filter}
					bind:validate={valueValidate}
					bind:focus={valueFocus}
					on:formattedValue={ ( e ) => formattedValueStore.setFormattedValue(rule.uid, e.detail )}
			></svelte:component>
		</span>

		<div class="acp-smartfilters-filter__actions">
			<button class="acp-smartfilters-filter__button -confirm" type="button" on:click={apply} use:enterKey={{preventDefault : true, stopPropagation:true}} on:enterpress={apply}>
				<i class="dashicons dashicons-yes"></i>
			</button>
			<button class="acp-smartfilters-filter__button -delete" type="button" on:click|preventDefault={deleteRule} use:enterKey on:enterpress={deleteRule}>
				<i class="dashicons dashicons-no-alt"></i>
			</button>

		</div>
	</div>
{:else}
	<div class="acp-smartfilters-filter -compact" role="button" tabindex="0" on:click={open} on:keypress={handleCompactKeyPress} on:keyup={handleCompactKeyUp} bind:this={compactComponent}>
		<span class="acp-smartfilters-filter__label">{filter.label}</span>
		<span class="acp-smartfilters-filter__operator -highlight">{getOperatorLabel( rule.operator )}</span>
		<span class="acp-smartfilters-filter__value">
			{#if $formattedValueStore[ rule.uid ] }
				{$formattedValueStore[ rule.uid ]}
			{:else}
				{#if rule.value }
					{Array.isArray( rule.value ) ? rule.value.join( ' - ' ) : rule.value }
				{/if}
			{/if}
			{#if operatorSuffix}
				{operatorSuffix}
			{/if}
		</span>

		<button class="acp-smartfilters-filter__delete" type="button" on:click|preventDefault|stopPropagation={deleteRule} use:enterKey={{preventDefault : true, stopPropagation:true}} on:enterpress={deleteRule}>
			<i class="dashicons dashicons-no-alt"></i>
		</button>

	</div>
{/if}