<script>
	import {currentSegment, segments} from "../store/segments";
	import {deleteSegment} from "../modules/segments/ajax";
	import {onMount} from "svelte";

	export let key;
	export let name;
	export let url;

	let linkElement = null;

	const deleteItem = ( key ) => {
		deleteSegment( key ).then( () => {
			segments.deleteSegment( key );
		} );
	}

	const updateTitle = () => {
		linkElement.setAttribute( 'title', linkElement.textContent )
	}

	onMount( () => {
		updateTitle();
	} )
</script>

<div class="ac-segment" data-key="{key}" data-name="{name}" class:-current={$currentSegment === key}>
	<a class="ac-segment__label" href="{url}" bind:this={linkElement}>{@html name}</a>

	<button class="ac-segment__action -delete" data-action="delete" on:click={ () => deleteItem(key)}>
		<span class="dashicons dashicons-no-alt"></span>
	</button>
</div>
