<script lang="ts">
    import {valueValidator} from "../../types/global";

    export let value: string = '';
    export const validate: valueValidator = (): boolean => {
        return input.reportValidity();
    };

    export const focus = (): void => {
        input.focus();
    }

    let input: HTMLInputElement;
</script>


<input type="text" bind:value={value} bind:this={input} required>

