<script lang="ts">
    import Select from "svelte-select";
    import {createEventDispatcher, onMount} from "svelte";
    import {fetchRemoteOptionsForColumn} from "../../ajax/select-values";
    import {FilterType} from "../../types/filters";
    import {valueValidator} from "../../types/global";
    import {SelectItemCollection} from "../../types/select-values";
    import FiltersMapper from "../../mapping/filters-mapper";
    import {getSearchTranslation} from "../../helpers/globals";

    export let value: string = '';
    export let filter: FilterType;
    export const validate: valueValidator = (): boolean => {
        return typeof value === 'string' && value !== '';
    };

    export const focus = (): void => {
        setTimeout(() => {
            handleFocus();
            listOpen = true;
        }, 100);
    }

    const i18n = getSearchTranslation();
    const dispatch = createEventDispatcher();

    let loading: boolean = false;
    let emptyLabel = i18n.no_search_results;
    let selectValue = null;
    let hideEmptyState: boolean = false;
    let listOpen:boolean = false;
    let items: SelectItemCollection = [];

    const groupBy = (item) => item.group;

    const handleFocus = () => {
        if (items.length === 0) {
            loading = true;
            hideEmptyState = true;

            fetchRemoteOptionsForColumn( filter.id ).then( retrievedItems => {
                items = FiltersMapper.fromSelect2( retrievedItems )
                hideEmptyState = false;
                loading = false;
			}).catch ( (d:string) => {
                emptyLabel = d;
                items = [];
                hideEmptyState = false;
			} );
        }
    }

    let changeHandler = (e) => {
        value = e.detail.value;
        dispatch('formattedValue', e.detail.label);
    }

    onMount(() => {

    });
</script>


<Select
		clearable={false}
		{groupBy}
		{items}
		{loading}
		{hideEmptyState}
		showChevron
		listAutoWidth={false}
		class="-wordpress"
		on:change={changeHandler}
		on:focus={handleFocus}
		bind:value={selectValue}
		bind:listOpen={listOpen}
		--list-z-index="1000"
		--width="200px"
>
	<div slot="empty">
		<div style="padding: 10px;">{emptyLabel}</div>
	</div>
</Select>
