import Media from "./placement/media";
import Taxonomy from "./placement/taxonomy";
import User from "./placement/user";
import Post from "./placement/post";
import MsUser from "./placement/ms-user";
export default class PlacementFactory {
    constructor(AcServices, Search, meta_type, resetFilters, activeFilters) {
        this.AcServices = AcServices;
        this.Search = Search;
        this.meta_type = meta_type;
        this.resetFilters = resetFilters;
        this.activeFilters = activeFilters;
        this.placements = {};
        this.register('media', new Media(this.resetFilters));
        this.register('term', new Taxonomy(this.resetFilters, this.activeFilters, this.Search));
        this.register('user', new User(this.resetFilters));
        this.register('ms-user', new MsUser(this.resetFilters));
        this.register('post', new Post(this.resetFilters));
        this.register('comment', new Post(this.resetFilters));
        AcServices.emitEvent('Search.PlacementFactory', this);
    }
    register(metaType, placement) {
        this.placements[metaType] = placement;
    }
    create() {
        const type = this.getListScreenType();
        return this.placements.hasOwnProperty(type) ? this.placements[type] : null;
    }
    getListScreenType() {
        switch (AC.list_screen) {
            case 'wp-ms_users':
                return 'ms-user';
            case 'wp-media':
                return 'media';
            default:
                return this.meta_type;
        }
    }
}
