export default class FiltersMapper {
    static sanitizeLabel(label) {
        let el = document.createElement('div');
        el.innerHTML = label;
        return el.innerText;
    }
    static fromSelect2(filters, group = null) {
        let result = [];
        filters.forEach(item => {
            if (item.hasOwnProperty('children')) {
                let groupItem = item;
                let groupedItems = FiltersMapper.fromSelect2(groupItem.children, groupItem.text);
                result = [...result, ...groupedItems];
            }
            else {
                let singleItem = item;
                let filterItem = {
                    value: singleItem.id.toString(),
                    label: FiltersMapper.sanitizeLabel(singleItem.text),
                };
                if (group) {
                    filterItem.group = group;
                }
                result.push(filterItem);
            }
        });
        return result;
    }
}
