<script lang="ts">
    import {getSearchConfig, getSearchTranslation} from "../helpers/globals";
    import {getFiltersConfig} from "../helpers/filters";
    import {createEventDispatcher, onDestroy, onMount} from "svelte";
    import {openRulesStore, rulesStore} from "../store/rules";
    import {createNewRuleForFilter} from "../helpers/rules";
    import SegmentContainer from "./SegmentContainer.svelte";
    import AcDropdown from "ACUi/acui-dropdown/AcDropdown.svelte";
    import AcDropdownItem from "ACUi/acui-dropdown/AcDropdownItem.svelte";
    import {FilterType} from "../types/filters";

    const i18n = getSearchTranslation();
    const filters = getFiltersConfig().getAll();
    const dispatch = createEventDispatcher();

    let config = getSearchConfig();
    let showFilters: boolean = false;
    let showSegments: boolean = false;
    let segmentButton: HTMLButtonElement;
    let dropDown:AcDropdown;

    const handleAddFilter = (e) => {
        if (!e.target?.classList.contains('button')) {
            return;
        }

        showSegments = false;
        showFilters = !showFilters;
    }

    const toggleSegments = async () => {
        showFilters = false;
        showSegments = !showSegments;
    }

    const closeSegments = () => {
        showSegments = false;
        segmentButton?.focus();
    }

    const selectFilter = (filter: FilterType) => {
        const rule = createNewRuleForFilter(filter);

        rulesStore.addRule(rule);
        showFilters = false;
        openRulesStore.open(rule.uid ?? '');
        dispatch('addFilter', rule);

        dropDown.toggle();
    }

    onMount(() => {
        document.addEventListener('keydown', (e: KeyboardEvent) => {
            if (e.key === 'Escape') {
                showFilters = false;
                closeSegments();
            }
        });
    });

</script>

<div class="acp-smartfilters__add" class:-has-segments={config.segments.enabled }>
	<div class="acp-smartfilters__add__group" on:keydown role="none">
		<AcDropdown --menu-z-index="30000" maxHeight="300px" bind:this={dropDown}>
			<button slot="trigger"
					on:click|preventDefault={handleAddFilter}
					on:keypress|preventDefault
					class="button" type="button">{i18n.add_filter}</button>

			{#each filters as filter, i}
				<AcDropdownItem value={filter} disabled on:click={() => selectFilter(filter) }>{filter.label}</AcDropdownItem>
			{/each}
		</AcDropdown>
		{#if config.segments.enabled }
			<button type="button" class="button acp-segments__toggle"
					class:-open={showSegments}
					on:click|preventDefault|stopPropagation={toggleSegments}
					bind:this={segmentButton}></button>
		{/if}
	</div>
	{#if showSegments}
		<SegmentContainer trigger={segmentButton} on:close={closeSegments}/>
	{/if}
</div>