<script lang="ts">
    import Select from "svelte-select";
    import {valueValidator} from "../../types/global";
    import FiltersMapper from "../../mapping/filters-mapper";
    import {createEventDispatcher, onMount} from "svelte";
    import {SelectItemCollection} from "../../types/select-values";
    import {FilterType} from "../../types/filters";

    export let value: string | null = '';
    export let filter: FilterType;

    export const validate: valueValidator = (): boolean => {
        return value !== '';
    };

    export const focus = (): void => {
        setTimeout(() => {
            listOpen = true;
        }, 100);
    }

    const dispatch = createEventDispatcher();

    let focused = false;
    let options: SelectItemCollection = [];
    let listOpen = false;
    let selectValue = null;

    let changeHandler = (e) => {
        value = e.detail.value;

        // TODO retrieve formatted value on page load
        dispatch('formattedValue', e.detail.label);
    }

    onMount(() => {
        if (!(typeof value === 'string' && value !== '')) {
            value = null;
        }
        options = filter.options ? FiltersMapper.fromSelect2(filter.options) : [];

        if (options.find(d => d.value === value)) {
            selectValue = {value: value};
        } else {
            value = null;
        }
        listOpen = true;
    });
</script>

<div class="">
	<Select
			listAutoWidth={false}
			clearable={false}
			items={options}
			showChevron
			class="-wordpress -acpsearch"
			on:change={changeHandler}
			bind:focus={focused}
			bind:value={selectValue}
			bind:listOpen={listOpen}
			--width="200px">

	</Select>
</div>
