import AbstractPlacement from "./abstract";
import { getFilterForm } from "../../../../filtering/js/components/form-factory";
import AcHtmlElement from "AC/helpers/html-element";
import { getFilterButton } from "../../../../filtering/js/components/button-factory";
export default class MsUser extends AbstractPlacement {
    place() {
        const tableTop = document.querySelector('.tablenav.top');
        const searchContainer = AcHtmlElement.find('.acp-smartfilters');
        let filterForm = getFilterForm();
        if (!tableTop || !searchContainer) {
            return;
        }
        const button = getFilterButton();
        filterForm
            .prepend(searchContainer.getElement())
            .addEventListener('submit', () => {
            const Search = AC_SERVICES.getService('Search');
            filterForm.append(Search.getHiddenValueInput().getElement());
        })
            .append(button.getElement())
            .append(this.resetFilters.getElement());
    }
}
