import AbstractPlacement from "./abstract";
import AcHtmlElement from "AC/helpers/html-element";
export default class Media extends AbstractPlacement {
    place() {
        const mediaFilterContainer = AcHtmlElement.find('.wp-filter');
        const searchModule = AcHtmlElement.find('.acp-smartfilters');
        if (!mediaFilterContainer || !searchModule) {
            return;
        }
        AcHtmlElement
            .create('div')
            .addClass('acp-smartfilter-actions')
            .appendSelfTo(mediaFilterContainer.getElement())
            .append(searchModule.getElement())
            .appendFound('.acp-filter-container')
            .appendFound('.wp-filter .filter-items label, .wp-filter .filter-items select')
            .appendFound('#post-query-submit')
            .append(this.resetFilters.getElement());
    }
}
